<template>
  <div class="edit_container">
    <Form ref="formCustom" :model="formCustom" :rules="ruleValidate" style="text-align: center">
      <FormItem prop="title" style="width: 200px;margin:0px auto;">
        <Input type="text" v-model="formCustom.title" placeholder="请输入标题"></Input>
      </FormItem>
      <FormItem>
        <p style="text-align: center;font-weight: bolder;font-size: 20px;margin: 5px">作者：{{formCustom.writer}}</p>
      </FormItem>
      <FormItem>
        <quill-editor
          v-model="formCustom.content"
          ref="myQuillEditor"
          :options="editorOption"
          @blur="onEditorBlur($event)" @focus="onEditorFocus($event)"
          @change="onEditorChange($event)">
        </quill-editor>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit('formCustom')">修改</Button>
        <Button @click="handleReset" style="margin-left: 8px">清空</Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
  import {quillEditor} from "vue-quill-editor"; //调用编辑器
  export default {
    name: "SaveText",
    inject: ['reload'],
    components: {
      quillEditor
    },
    data() {
      return {
        editorOption: {},
        formCustom: {
          content: ``,
          title: '',
          writer: '',
          writerId:''
        },
        ruleValidate: {
          title: [
            {required: true, message: '标题不为空', trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      onEditorReady(editor) { // 准备编辑器

      },
      onEditorBlur() {
      }, // 失去焦点事件
      onEditorFocus() {
      }, // 获得焦点事件
      onEditorChange() {
      }, // 内容改变事件
      // 转码
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (this.formCustom.content == '') {
            this.$Message.error("内容不能为空")
          } else if (valid) {
            var params = new URLSearchParams();
            params.append('textContent', this.formCustom.content);
            params.append('textWriterid', this.formCustom.writerId);
            params.append('textTitle', this.formCustom.title);
            this.axios.post('text', params).then(res => {
              if (res.data.code === 100) {
                this.$Message.success('保存成功');
                this.$store.commit('setTextNumAdd')
                this.$router.push("textsList")
              } else {
                this.$Message.error("保存失败")
              }
            }).catch(error => {
              this.$Message.warning("保存文章失败")
            })
          } else {
            this.$Message.error('内容不符合要求');
          }
        })
      },
      handleReset() {
        this.formCustom.content = '',
          this.formCustom.title = ''
      }
    },
    computed: {
      editor() {
        return this.$refs.myQuillEditor.quill;
      },
    }, mounted() {
      this.formCustom.writer = this.$store.state.userInfo.userName;
      this.formCustom.writerId = this.$store.state.userInfo.userId;
    }
  }
</script>

<style scoped>

</style>



